	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import aboutus from '@/views/aboutus/list'
	import address from '@/views/address/list'
	import discussmeishixinxi from '@/views/discussmeishixinxi/list'
	import discussbingrenzhuanqu from '@/views/discussbingrenzhuanqu/list'
	import storeup from '@/views/storeup/list'
	import users from '@/views/users/list'
	import cart from '@/views/cart/list'
	import shangjia from '@/views/shangjia/list'
	import meishifenlei from '@/views/meishifenlei/list'
	import yonghu from '@/views/yonghu/list'
	import meishixinxi from '@/views/meishixinxi/list'
	import orders from '@/views/orders/list'
	import config from '@/views/config/list'
	import bingrenzhuanqu from '@/views/bingrenzhuanqu/list'
	import shangjiaRegister from '@/views/shangjia/register'
	import shangjiaCenter from '@/views/shangjia/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/shangjiaCenter',
			name: '',
			component: shangjiaCenter
		}
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/aboutus',
			name: '',
			component: aboutus
		}
		,{
			path: '/address',
			name: '',
			component: address
		}
		,{
			path: '/discussmeishixinxi',
			name: '',
			component: discussmeishixinxi
		}
		,{
			path: '/discussbingrenzhuanqu',
			name: '',
			component: discussbingrenzhuanqu
		}
		,{
			path: '/storeup',
			name: '',
			component: storeup
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		,{
			path: '/cart',
			name: '',
			component: cart
		}
		,{
			path: '/shangjia',
			name: '',
			component: shangjia
		}
		,{
			path: '/meishifenlei',
			name: '',
			component: meishifenlei
		}
		,{
			path: '/yonghu',
			name: '',
			component: yonghu
		}
		,{
			path: '/meishixinxi',
			name: '',
			component: meishixinxi
		}
		,{
			path: '/orders',
			name: '',
			component: orders
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		,{
			path: '/bingrenzhuanqu',
			name: '',
			component: bingrenzhuanqu
		}
		]
	},
	{
		path: '/shangjiaRegister',
		name: '',
		component: shangjiaRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
